<script>
import Constant from '@/Constant'
import itemslist from '@/views/DataSet.json' 
// import InnerOption from '@/components/InnerOption';

export default {
    name :'GoodsDetail',
    data : function() {
        return {
            id : 0,
            items : itemslist.items,
            baseimg : null,
            count : 1,
            optionlist: [],
            selected:''
        }
    },
    created : function() {
        this.id = this.$route.params.id;
    },
    components : {
        // InnerOption
    },
    computed : {
        item : function() {
            let id = this.id;
            let arr = this.items.filter(function(item) {
                return item.id == id;
            });

            if (arr.length == 1) return arr[0];
            else return {};
        },
    }, 
    methods : {
        eventOpenModal:function(e){
            this.$store.dispatch(Constant.MODAL_OPEN,{modalView:'ModalCart',modalSize:e})
        },
        minus:function(){
            if(this.count > 1) return this.count -= 1 
        },
        doAdd: function () {
            console.log(this.selected)

            // 새로운 리스트에 추가하기
            this.optionlist.push({
                id: this.selected.id,
                name: this.selected.name,
                value: this.selected.value,
                count: 1
            })
        }
    
    }
}
</script>
